export default {
  layout: "Adm-content",
  mounted() {
    this.getdepartment();
  },
  data() {
    return {
      departments: [],
      department: {
        department_name: "",
       
      },
      errors: {},
    };
  },
  methods: {
    getdepartment() {
      this.$axios
        .$get("/admission/department-show")
        .then((res) => {
          this.departments = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    departmentEdit(id) {
      $("#departmentUpdate").modal("show");
      this.$axios
        .$get("/admission/department-edit/" + id)
        .then((res) => {
          this.department = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    departmentUpdate() {
      this.$axios
        .$post("/admission/department-update/" + this.department.id, this.department)
        .then((res) => {
          this.getdepartment();
          $("#departmentUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deletedepartment(id) {
      if (confirm("Are you sure to delete this department?")) {
        this.$axios
          .$get("/admission/department-delete/" + id)
          .then((res) => {
            this.getdepartment();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    departmentStatus(id) {
      this.$axios
        .$get("/admission/department-status/" + id)
        .then((res) => {
          this.getdepartment();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
